// Import external
import React from 'react';
import {StyleProp, TextProps, TextStyle, View, ViewStyle} from 'react-native';
import {isUndefined} from 'lodash';
import {
  Icon,
  Text,
  TopNavigation,
  TopNavigationAction,
} from '@ui-kitten/components';
import {RenderProp} from '@ui-kitten/components/devsupport';
import {useNavigation} from '@react-navigation/native';
import {SafeAreaView} from 'react-native-safe-area-context';
import {Color, Font} from '../../../constants';
import TextCM from '../Text';

type Props = {
  style?: StyleProp<ViewStyle>;
  title?: string;
  subtitle?: RenderProp<TextProps> | React.ReactText | undefined;
  onPressIconLeft?: () => void;
  viewIconLeft?: string;
  contentStyle?: StyleProp<ViewStyle>;
  titleStyle?: StyleProp<TextStyle>;
  renderContentRight?: () => React.ReactElement;
  appearance?: 'default' | 'control';
  alignment?: 'start' | 'center';
  fillIconBackLeft?: string;
};

const HeaderCM = ({
  style,
  onPressIconLeft,
  viewIconLeft = 'arrow-back',
  title = 'quangnh39',
  contentStyle,
  renderContentRight,
  appearance = 'default',
  alignment = 'center',
  subtitle = '',
  fillIconBackLeft = '#ffffff',
  titleStyle,
}: Props) => {
  const navigation = useNavigation();

  const handleOnpressIconLeft = () => {
    if (isUndefined(onPressIconLeft)) {
      navigation.goBack();
    } else {
      onPressIconLeft();
    }
  };

  const renderBackAction = () => (
    <TopNavigationAction
      hitSlop={15}
      onPress={handleOnpressIconLeft}
      icon={<Icon name={viewIconLeft} fill={fillIconBackLeft} />}
    />
  );

  return (
    <SafeAreaView
      edges={['top']}
      style={[
        {
          backgroundColor: Color.BgPrimary,
        },
        style,
      ]}>
      <TopNavigation
        title={props => (
          <TextCM
            {...props}
            numberOfLines={1}
            style={[
              {
                color: '#fff',
                fontFamily: Font.InterSemiBold600,
                fontSize: 18,
                width: 230,
              },
              titleStyle,
            ]}>
            {title}
          </TextCM>
        )}
        appearance={appearance}
        accessoryLeft={renderBackAction}
        accessoryRight={renderContentRight}
        alignment={alignment}
        subtitle={subtitle}
        style={[
          {
            backgroundColor: Color.BgPrimary,
            minHeight: 40,
          },
          contentStyle,
        ]}
      />
    </SafeAreaView>
  );
};

export default HeaderCM;
